<template>

    <div>
        <Greeting></Greeting>
        <TodoList :todos="todos"></TodoList>
    </div>

</template>


<script>
    import Greeting from './Greeting'
    import TodoList from './TodoList'

    export default {
        name: 'page',
        components: { Greeting, TodoList },
          // props are items that exist elsewhere //
          props: ['item'],
        data() {
            return {
                todos: []
            }
        }
    }

</script>


