<template>
    <div>
        <TodoForm></TodoForm>
        <ul v-for="(todo, index) in todos">
            <li @click="removeTodo(index)">{{todo}}

            </li>
        </ul>
    </div>
</template>

<script>
    import TodoForm from './TodoForm'

    export default {
        name: 'todo-list',
        components: { TodoForm },
        // props: ['todos'],
        data() {
            return {
                todos: [],
            }

        },
        methods: {
            removeTodo: function (index) {
                this.todos.splice(index, 1)
            }
        }
    }

</script>


