<template>


    <form @submit.prevent="submitTodo(todo)">
    <input v-model="todo">
    <button>Add Todo</button>
</form>


</template>

<script>
    export default {
        name: 'todo-form',
        data() {
            return {
                todo: '',
            }
        },

        methods: {
            submitTodo() {
                this.$parent.todos.push(this.todo)
                this.todo=''
            }

        }
    }
</script>


